import React from 'react';
import { Col } from 'react-bootstrap';
import { Service } from '../models/models';


export interface IServiceCardProps {
  Service: Service;
}

export interface IServiceCardState {

}

export default class ServiceCardLarge extends React.Component<IServiceCardProps, IServiceCardState> {

  constructor(props: IServiceCardProps) {
    super(props);
    this.state = {};
  }

  public render(): React.ReactElement<IServiceCardProps> {
    return (
      <Col xs={12} md={12} lg={12} className="serviceCard">
        <h2>{this.props.Service.Title}</h2>
          
      </Col>
    );
  }

}